:root {
  --background: #202931;
  --foreground: #ededed;
}
body {
  color: var(--foreground);
  background: var(--background);
  font-family: "Poppins", sans-serif;
  margin: 0;
}
img {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
  pointer-events: auto;
}
.nav-links {
  display: flex;
  list-style: none;
  gap: 15px;
}
.nav-links a {
  color: white;
  text-decoration: none;
  font-weight: bold;
}
.nav-links a:hover {
  text-decoration: underline;
}
::selection {
  background-color: rgba(55, 151, 230, 0.5);
  color: rgb(209, 234, 255);
}
::-moz-selection {
  background-color: rgba(55, 151, 230, 0.5);
  color: rgb(209, 234, 255);
}
::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 6px;
}
::-webkit-scrollbar-thumb {
  background: #959595;
  border-radius: 6px;
}
.bouncing-loader {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  width: 90px;
  height: 50px;
  position: fixed;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.bouncing-loader-s {
  position: unset !important;
  margin: 50px 0 150px 50%;
}
.bouncing-loader-s div {
  background-color: var(--s-third-color) !important;
}
.bouncing-loader div {
  width: 15px;
  height: 15px;
  background-color: #52525c;
  border-radius: 50%;
  animation: bounce 1s infinite ease-in-out;
}
.bouncing-loader div:nth-child(1) {
  animation-delay: 0s;
}
.bouncing-loader div:nth-child(2) {
  animation-delay: 0.2s;
}
.bouncing-loader div:nth-child(3) {
  animation-delay: 0.4s;
}
@keyframes bounce {
  0%,
  80%,
  100% {
    transform: scale(0.9);
  }
  50% {
    transform: scale(1.7);
  }
}
.btn {
  padding: 10px 20px;
  background-color: #2d2e38;
  width: fit-content;
  border-radius: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}
.btn:hover {
  background-color: #454650;
  transform: scale(1.03);
}
.project {
  position: relative;
  display: flex;
  gap: 2vw;
}
.project.reverse {
  flex-direction: row-reverse;
}
.project > :first-child {
  position: relative;
  aspect-ratio: 16/9;
  width: 40%;
}
.project > :last-child {
  position: relative;
  width: 60%;
}
.project img {
  border-radius: 10px;
}
.project h3 {
  font-size: 3rem;
  font-weight: 800;
}
.project p {
  margin: 20px 0 40px;
}
